<template>
  <div>
    <BannerView />
    <div class="main-container">
      <h1>Formatar uma planilha nunca foi tão fácil</h1>

      <div class="flex-container">
        <v-card class="card">
          <v-card-title>
            <span class="card-title">Formatador de planilhas</span>
          </v-card-title>
          <v-card-text>
            <p>
              Carregue sua planilha e deixe que nosso sistema a formate automaticamente. Simplifique suas tarefas e economize tempo!
            </p>
          </v-card-text>
        </v-card>

        <video src="/video/video.mp4" width="540" height="360" autoplay loop muted class="video"></video>

        <v-btn @click="showForm" class="btn-format">Formatar planilha</v-btn>
      </div>

      <v-tooltip location="bottom">
        <template v-slot:activator="{ props }">
          <v-btn class="btn-tooltip" v-bind="props" color="primary" dark>Como formatar sua planilha?</v-btn>
        </template>
        <v-card class="tooltip-card">
          <v-card-title>
            <span class="tooltip-title">Instruções de Formatação</span>
          </v-card-title>
          <v-card-text>
            <ol>
              <li>Clique no botão 'Formatar Planilha' à sua direita.</li>
              <li>Escolha o tipo de relatório que deseja formatar.</li>
              <li>Escolha a planilha na qual deseja aplicar a formatação.</li>
              <li>Clique em 'Formatar'.</li>
              <li>Agora é só aguardar que seu arquivo já vai estar pronto.</li>
            </ol>
          </v-card-text>
        </v-card>
      </v-tooltip>

      <div class="spacer"></div>
      <FormDialog
        :visible="formVisible"
        :report_type="type"
        :file_name="file"
        @format="formatSheet"
        @cancel="closeForm"
      />
      <v-alert v-if="errorMessage" type="error">{{ errorMessage }}</v-alert>
      <v-alert v-if="successMessage" type="success">{{ successMessage }}</v-alert>
    </div>
  </div>
</template>

<script>
import BannerView from './BannerView.vue';
import FormDialog from './FormDialog.vue';

export default {
  name: 'HomeView',
  components: {
    BannerView,
    FormDialog,
  },
  data() {
    return {
      formVisible: false,
      file: null,
      type: null,
      errorMessage: null,
      successMessage: null,
    };
  },
  methods: {
    showForm() {
      this.formVisible = true;
    },
    async formatSheet(file, type) {
      this.file = file;
      this.type = type.type;
      this.errorMessage = null; // Limpa mensagem de erro anterior
      this.successMessage = null; // Limpa mensagem de sucesso anterior

      try {
        const formData = new FormData();
        formData.append("report", this.type);
        formData.append("myFile", this.file);

        const response = await fetch('http://localhost:3000/upload', {
          method: 'POST',
          body: formData,
        });

        if (!response.ok) {
          throw new Error(`Error: ${response.status}`);
        }

        // Trata a resposta como blob (arquivo binário)
        const blob = await response.blob();
        const url = window.URL.createObjectURL(blob);
        
        const a = document.createElement('a');
        a.href = url;
        a.download = 'relatorio_formatado.xlsx'; // Nome do arquivo a ser baixado
        document.body.appendChild(a);
        a.click();
        document.body.removeChild(a);
        window.URL.revokeObjectURL(url);

        this.successMessage = 'Planilha formatada e download iniciado!';
        this.closeForm();
      } catch (error) {
        this.errorMessage = 'Erro ao formatar o arquivo. Por favor, tente novamente.';
        console.error(error); // Para debugar no console
      }
    },
    closeForm() {
      this.formVisible = false;
    }
  }
}
</script>

<style scoped>
.main-container {
  background-color: rgb(206, 206, 206);
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1 {
  margin-top: 20px;
  color: #1C4053;
  font-size: 40px;
  font-weight: bolder;
}

.flex-container {
  display: flex;
  align-items: flex-start; 
  justify-content: space-around; 
  width: 100%; 
  max-width: 1200px; 
  margin-top: 50px; 
  position: relative; 
}

.card {
  background-color: #1C4053;
  color: aliceblue;
  width: 40%;
  padding: 30px;
  margin-top: 100px;
  margin-left: -142px;
  border-bottom: 4px solid rgba(5, 6, 65, 0.555);
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  transition: all .2s ease-in-out;
}

.tooltip-card {
  width: 400px; 
  background: transparent;
  color: aliceblue;
}

.tooltip-title {
  font-weight: bold;
  color: aliceblue;
}

.btn-format {
  background-color: rgb(5, 6, 65);
  color: aliceblue;
  font-weight: bold;
  height: 50px;
  margin-top: 280px;
  position: absolute;
  right: -70px;
}

.btn-tooltip {
  align-self: flex-start;
  margin-top: -50px;
  margin-left: 40px;
}

.spacer {
  height: 300px;
}

.video {
  width: 540px;  
  height: 360px; 
  border: 5px solid #1C4053; 
  border-radius: 10px; 
  object-fit: cover;
  display: block; 
  margin: 0; 
  padding: 0; 
  margin-right: 200px; 
}
</style>
